import React, { ReactNode } from 'react';
import{ Link as Scroll } from 'react-scroll'

interface LinkProps {
  // Propsの型定義
  to: string;
  children: ReactNode; // children プロパティを追加
}

/* スクロールリンクのProgramming要ラッパー */

const LinkP: React.FC<LinkProps> = (props) => {
  
  return <Scroll to={`${props.to}`} smooth={ true } duration={ 300 } offset={ -80 } >セクション1へジャンプ</Scroll>;
};

interface Component2Props {
  // 別のコンポーネントのPropsの型定義
}

const Component2: React.FC<Component2Props> = (props) => {
  // 別のコンポーネントのロジック
  return <div>Component 2</div>;
};

// 別の関数コンポーネント
const Component3: React.FC = () => {
  return <div>Component 3</div>;
};

export { LinkP, Component2, Component3 };
